.form {
    padding: 1rem;
    background-color: #C4C4C4;
    border-radius: 1rem;
    border: .5rem solid #FFA630;
  }
  
  .control {
    margin-bottom: 0.5rem;
    background-color: #C4C4C4;
    color: black;
  }
  .control input{
    color: black;
  }
  .control label {
    display: block;
    font-size: 1.25rem;
    color: black;
    font-family:cursive;
    font-stretch: expanded;
    margin-bottom: 0.5rem;
    background-color: #C4C4C4;
  }
  
  .control input,
  .control textarea {
    display: block;
    font: inherit;
    border-radius: 4px;
    border: 1px solid #ccc;
    padding: 0.25rem;
    width: 100%;
    background-color: #f7f4f4;
  }
  
  .actions {
    margin-top: 1rem;
    text-align: right;
    background-color: #C4C4C4;
  }
  
  .actions button {
    font: inherit;
    cursor: pointer;
    border-color: #FFA630;
    background-color: #FAFAFF;
    color: #DF2935;
    padding: 0.5rem 1.5rem;
    border: 3px solid #FFA630;
    border-radius: 4px;
    font-weight:800;
  }
  
  .actions button:hover,
  .actions button:active {
    background-color: #fcca7e;
    border-color: #fcca7e;
    border-radius: 1rem;
  }