

.item {
    margin: 1rem 0;
    border: .5rem solid #FFA630;
    border-radius: 1rem;
    
  }
  
  
  .content {
    text-align: center;
    background-color: #FAFAFF;
    padding: 1rem;
  }

  .content p{
    background-color: #FAFAFF;
  }
  
  .content h3 {
    font-size: 1.25rem;
    color: black;
    font-family:cursive;
    font-stretch: expanded;
    background-color: #FAFAFF;
  }
  
  .actions {
    padding: 1.5rem;
    text-align: center;
    background-color: #FAFAFF;

  }
  
  .actions button {
    margin: 0.5rem;
    font: inherit;
    cursor: pointer;
    color: #DF2935;
    border: 3px solid #FFA630;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight:800;
    background-color: transparent;
    padding: 0.5rem 1.5rem;
    background-color: #FAFAFF;
    border-radius: 4px;
  }
  
  .actions button:hover,
  .actions button:active {
    background-color: #fcca7e;
  }

  .section{
    font-size: 2rem;
    color: #FAFAFF;
    font-family:cursive;
    font-stretch: expanded;
  }

  .btnxyz{
    text-decoration: none;
    font-size: 1.5rem;
    color: #FAFAFF;
    background-color: #92140C;
    border-color: #92140C;
    font-family: Poppins;
    font-style: normal;
    border-radius: 1rem;
}
.btnxyz:hover{
    background-color: #FFA630;
    border-color: #FFA630;
    border-radius: 1rem;
  }

.contsectop{
  display: flex;
  justify-content:space-around;
}

.list {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}